<template>
  <div id="app">
    <!--<img src="./assets/logo.png">-->
    <transition  name="el-fade-in-linear">
      <router-view/>
    </transition>
  </div>
</template>

<script>
  export default {
    name: 'App'
  }
</script>
<style>
  /*全局变量*/
  * {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
  .clearfix:before,
  .clearfix:after {
    content: '';
    display: block;
    height: 0;
    clear: both;
  }
  .clearfix {
    zoom: 1;
  }
  a {
    text-decoration: none;
  }
  .text-search-main .el-input input,
  .file-search-main .el-input input {
    height: 70px;
    line-height: 70px;
    font-size: 24px;
    color: #fff;
    background-color: transparent !important;
    border: none;
  }

  .el-input-group__append {
    background-color: transparent;
  }

  .el-submenu__title {
    color: #fff;
  }

  .file-search-main .el-upload--text {
    width: 100%;
  }
  .el-upload-dragger {
    width: 100%;
    background-color: rgba(255,255,255, .3);
  }
  .el-main {
    padding-bottom: 0;
  }

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }


</style>



