<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    .el-main {
        width: 1240px;
        margin: 0 auto;
        overflow: hidden;
    }

    .search-box {
        width: 900px;
        height: 350px;
        margin: 0 auto;
        margin-top: 220px;
    }

    .search-box__hd {

        .h1-title {
            color: #fff;
            font-size: 38px;
            letter-spacing: 5px;
        }
    }

    .text-search-main,
    .file-search-main {
        margin-top: 40px;

        .el-input {

            input {
                height: 70px;
                line-height: 70px;
                background-color: transparent !important;
            }
        }

        .el-btn {
            font-size: 28px;
            margin: 0;
            border: none;
            -webkit-border-radius: 0;
            -moz-border-radius: 0;
            border-radius: 0;
            color: #fff;
            padding: 12px 35px;
        }
        .el-btn-upload {
            background-color: transparent;

            &:before {
                content: '';
                display: inline-block;
                width: 28px;
                height: 28px;
                background: url("../assets/icon-video.png") no-repeat;
                background-size: 100%;
            }
        }
        .el-btn-search {
            background-color: #4c6aff;
        }
    }

    .hot-search {
        margin-top: 40px;
        color: #fff;

        .item {
            margin-left: 30px;
            cursor: pointer;
        }
    }

    .text-search-main {
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        border: 1px solid #e6e6e6;
        -webkit-border-radius: 20px;
        -moz-border-radius: 20px;
        border-radius: 20px;
        overflow: hidden;
    }

    .file-search-main {

        .upload-btn {
            border: none;
            background-color: #4c6aff;
            height: 70px;
            line-height: 70px;
            width: 200px;
            padding: 0;
            font-size: 20px;
            -webkit-border-radius: 20px;
            -moz-border-radius: 20px;
            border-radius: 20px;
        }

        .file-search-input {
            display: -webkit-box;
            display: -webkit-flex;
            display: flex;
            border: 1px solid #e6e6e6;
            -webkit-border-radius: 20px;
            -moz-border-radius: 20px;
            border-radius: 20px;
            overflow: hidden;
        }

        .drag-wrap {
            margin-top: 10px;
        }

        .el-upload--text {
            width: 100%;
        }

        .el-upload-dragger {
            width: 100%;
            background-color: rgba(255, 255, 255, .3);
        }

        .el-upload__text {
            color: #fff;
        }
    }

    .hot-content-wrap {
        margin-top: 240px;
        padding-top: 60px;
    }

    .hot-content-main {

        .h2-title {
            text-align: left;
        }

        .hot-content__bd {
            margin-top: 30px;
        }

        .hot-item {

            .item__bd {

                .img-wrap {
                    border: 1px solid #ccc;
                    -webkit-border-radius: 6px;
                    -moz-border-radius: 6px;
                    border-radius: 6px;
                    width: 182px;
                    height: 257px;
                }

                .img {
                    width: 100%;
                    height: 100%;
                }
            }

            .item__ft {
                margin-top: 15px;
                text-align: left;
                padding-left: 10px;
                display: -webkit-box;
                display: -webkit-flex;
                display: flex;
                align-items: center;
            }

            .source {
                display: inline-block;
                width: 40px;
                height: 40px;
                margin-right: 20px;
                position: relative;

                .img-wrap {
                    display: inline-block;
                    width: 100%;
                    height: 100%;
                    -webkit-border-radius: 50%;
                    -moz-border-radius: 50%;
                    border-radius: 50%;
                    overflow: hidden;
                    border: 1px solid #ccc;
                }

                .source-img {
                    width: 100%;
                    height: 100%;
                }
                .icon-infringement {
                    display: inline-block;
                    width: 22px;
                    height: 22px;
                    background: url("../assets/icon-infringement.png") no-repeat;
                    position: absolute;
                    right: -12px;
                    bottom: 0;
                }
            }
        }
        /* 可以设置不同的进入和离开动画 */
        /* 设置持续时间和动画函数 */
        .slide-fade-enter-active {
            transition: all .3s ease;
        }
        .slide-fade-leave-active {
            transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
        }
        .slide-fade-enter, .slide-fade-leave-to
            /* .slide-fade-leave-active for below version 2.1.8 */
        {
            transform: translateX(10px);
            opacity: 0;
        }
    }
    .item-link{
        cursor: pointer;
    }
</style>
<template>
    <el-main>
        <!-- 搜索框 -->
        <div class="search-box">
            <div class="search-box__hd">
                <h1 class="h1-title">侵权搜索</h1>
            </div>
            <!--<transition name="el-zoom-in-center">-->
            <!--<transition name="bounceInLeft">-->
            <transition name="slide-fade" mode="in-out">
                <!-- 搜索模式1 ：输入 -->
                <div class="text-search-wrap" v-if="mode === 1">
                    <div class="text-search-main">
                        <el-input placeholder="请输入搜索内容" v-model="searchText" class="el-input">
                        </el-input>
                        <el-button class="el-btn el-btn-upload" slot="append" @click="setMode"></el-button>
                        <el-button class="el-btn el-btn-search" slot="append" icon="el-icon-search"
                                   @click="search"></el-button>
                    </div>
                    <div class="hot-search">
                        热门搜索:
                        <span class="item" v-for="v in hotsearch" @click="hotToListPage(v)">{{v}}</span>

                    </div>
                </div>
                <!-- 搜索模式2：上传文件 -->
                <div class="file-search-wrap" v-if="mode === 2">
                    <div class="file-search-main">
                        <el-row>
                            <el-col :span="7">
                                <el-upload
                                        class="upload-demo"
                                        action="https://jsonplaceholder.typicode.com/posts/"
                                        :on-preview="handlePreview"
                                        :on-remove="handleRemove"
                                        :before-remove="beforeRemove"
                                        multiple
                                        :limit="3"
                                        :on-exceed="handleExceed"
                                        :file-list="fileList">
                                    <el-button class="upload-btn" size="small" type="primary">点击上传</el-button>
                                    <!--<div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>-->
                                </el-upload>
                            </el-col>
                            <el-col :span="17">
                                <div class="file-search-input">
                                    <el-input placeholder="请输入搜索网址" class="el-input">
                                    </el-input>
                                    <el-button class="el-btn el-btn-search" slot="append" icon="el-icon-search"
                                               @click="search"></el-button>
                                </div>
                            </el-col>
                        </el-row>
                        <el-row class="drag-wrap">
                            <el-col :span="24">
                                <el-upload
                                        class="upload-drag"
                                        drag
                                        action="https://jsonplaceholder.typicode.com/posts/"
                                        multiple>
                                    <i class="el-icon-upload"></i>
                                    <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                                    <!--<div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>-->
                                </el-upload>
                            </el-col>
                        </el-row>
                    </div>
                </div>
            </transition>
        </div>
        <!--<router-link to="/index/list">跳转list</router-link>-->
        <!-- 热门内容 -->
        <div class="hot-content-wrap">
            <div class="hot-content-main">
                <div class="hot-content__hd">
                    <h2 class="h2-title">热门被侵权内容</h2>
                </div>
                <div class="hot-content__bd">
                    <el-row class="hot-list">
                        <el-col v-for="(value, key, index) in hotlist" :span="4" class="hot-item">
                            <div @click="hotToListPage(value.keyword)" class="item-link">
                                <div class="item__hd"></div>
                                <div class="item__bd">
                                    <div class="img-wrap">
                                        <img :src="value.cover" alt="" class="img">
                                    </div>
                                    <!--<img src="../assets/demo-img.png" alt="" class="img">-->
                                </div>
                                <div class="item__ft">
                                    <div class="source">
                                        <div class="img-wrap">
                                            <!--<img src="../assets/vue-logo.png" alt="" class="source-img">-->
                                            <img :src="value.avator" alt="" class="source-img">
                                        </div>
                                        <span class="icon icon-infringement"></span>
                                    </div>
                                    <span class="name">{{value.name}}</span>
                                </div>
                            </div>
                        </el-col>
                    </el-row>
                </div>
                <div class="hot-content__ft"></div>
            </div>
        </div>
    </el-main>
</template>

<script>
	export default {
		name: 'List',
		data() {
			return {
				mode: 1,
				isMode1: false,
				isMode2: true,
				fileList: [
					// {name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}
				],
				hotsearch: [
					'你能不能', '皮卡皮卡Pikachu', '赛马的正确打开方式', '有啥不一样'
				],
				searchText: '',
				hotlist: [
                    {avator:'https://js2.a.yximgs.com/uhead/AB/2018/07/30/12/BMjAxODA3MzAxMjUyNDRfNjUxMzA2NzMxXzJfaGQxMzlfNjQ0_s.jpg',cover:'https://js2.a.yximgs.com/upic/2018/03/29/18/BMjAxODAzMjkxODQ3NDhfNjUxMzA2NzMxXzU2NzAwNTc4MjdfMl8z_B302c638fdba6e80c9cbec4a429eed046.jpg',name:'你能不能',keyword:'你能不能'},
                    {avator:'https://tx2.a.yximgs.com/uhead/AB/2018/08/24/22/BMjAxODA4MjQyMjQ2NTBfNDAzMjQ2NjExXzJfaGQzMTFfODM5_s.jpg',cover:'https://js2.a.yximgs.com/upic/2018/08/28/09/BMjAxODA4MjgwOTI0MDNfNDAzMjQ2NjExXzc3OTgwMTUwMzNfMl8z_Be548c76b997d3f1ed268e3ac4ef015ce.jpg',name:'皮卡皮卡Pikachu',keyword:'皮卡皮卡Pikachu'},
                    {avator:'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2569469568,1327205126&fm=26&gp=0.jpg',cover:'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2569469568,1327205126&fm=26&gp=0.jpg',name:'赛马',keyword:'赛马的正确打开方式'},
                    {avator:'https://js2.a.yximgs.com/uhead/AB/2018/01/23/14/BMjAxODAxMjMxNDIwMjdfNDY0NDQ3MjM2XzFfaGQ3NTFfNTk5_s.jpg',cover:'https://tx2.a.yximgs.com/upic/2018/09/09/16/BMjAxODA5MDkxNjQ3MjRfNDY0NDQ3MjM2Xzc5NjY0MDM4NDNfMV8z_B8bf23bb1adb0d2d6afce60ded3f95e46.jpg',name:'有啥不一样',keyword:'有啥不一样'},
                    {avator:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1536587693339&di=0f54ebf58a44765a7bf937728f70fb63&imgtype=0&src=http%3A%2F%2Fqimg.hxnews.com%2F2018%2F0725%2F1532505358883.jpeg',cover:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1536587693339&di=0f54ebf58a44765a7bf937728f70fb63&imgtype=0&src=http%3A%2F%2Fqimg.hxnews.com%2F2018%2F0725%2F1532505358883.jpeg',name:'摩登兄弟',keyword:'摩登兄弟'},
                    {avator:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1536587919248&di=e59a9149b48c4a7929fb04a7fa46f4c7&imgtype=0&src=http%3A%2F%2Fdown.52pk.com%2Fuploads%2F180416%2F5051_134814_1_lit.png',cover:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1536587919248&di=e59a9149b48c4a7929fb04a7fa46f4c7&imgtype=0&src=http%3A%2F%2Fdown.52pk.com%2Fuploads%2F180416%2F5051_134814_1_lit.png',name:'黑脸v',keyword:'黑脸v'},
                ],keyword:''
			};
		},
		methods: {
			setMode() {
				this.mode = 2;
			},
			handleRemove(file, fileList) {
				console.log(file, fileList);
			},
			handlePreview(file) {
				console.log(file);
			},
			handleExceed(files, fileList) {
				this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
			},
			beforeRemove(file, fileList) {
				return this.$confirm(`确定移除 ${ file.name }？`);
			},
			search() {
				this.$router.push({name: 'listPage',params: { keyword: this.searchText}})
			},
			hotToListPage(v){
				this.$router.push({name: 'listPage',params: { keyword: v }})
            },
		},
		mounted() {
//			this.$axios.get('http://192.168.1.114:5000/api/search/?genre=hot').then(data => {
//				console.log(data);
//				if (data.data.code === 200) {
//					this.hotlist = data.data.data;
//				}
//			})
		}
	}
</script>



