<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    .el-main {
        margin: 0 -20px;
        padding: 20px 0 0;
        overflow: hidden;
    }

    .search-result-wrap {
        margin-top: 300px;
        background-color: #fff;
    }

    .search-result-main {
        width: 1240px;
        padding: 0 20px;
        margin: 0 auto;
    }

    .search-result__hd {
        padding-bottom: 30px;
        padding-top: 60px;
        border-bottom: 1px solid #e8e8e8;
    }

    .result-target-main {
        position: relative;
    }

    .result-target__lt {
        position: absolute;
        left: 0;
        bottom: 0;

        .music,
        .video {
            /*display: -webkit-box;*/
            /*display: -webkit-flex;*/
            /*display: flex;*/
            /*align-items: end;*/

            .img-wrap {
                display: inline-block;
                vertical-align: bottom;
            }

            .name {
                display: inline-block;
                padding-bottom: 10px;
                line-height: 30px;
                text-align: left;
            }
        }
    }

    .result-target__rt {
        text-align: right;
    }

    .result-item {
        margin-top: 30px;

        .item__bd {

            .img-wrap {
                border: 1px solid #ccc;
                -webkit-border-radius: 6px;
                -moz-border-radius: 6px;
                border-radius: 6px;
                width: 260px;
                height: 370px;
            }
            .img {
                width: 100%;
                height: 100%;
            }
        }

        .item__ft {
            margin-top: 15px;
            text-align: left;
            padding-left: 20px;
            display: -webkit-box;
            display: -webkit-flex;
            display: flex;
            align-items: center;
        }

        .source {
            display: inline-block;
            width: 40px;
            height: 40px;
            margin-right: 20px;
            position: relative;

            .img-wrap {
                display: inline-block;
                width: 100%;
                height: 100%;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                border-radius: 50%;
                overflow: hidden;
                border: 1px solid #ccc;
            }

            .source-img {
                width: 100%;
                height: 100%;
            }
            .icon-infringement {
                display: inline-block;
                width: 22px;
                height: 22px;
                background: url("../assets/icon-infringement.png") no-repeat;
                position: absolute;
                right: -12px;
                bottom: 0;
            }
        }

        .name {
            color: #000;
        }
        .item-link {
            cursor: pointer;
        }
    }
    .mt40{
        margin-top: 40px;
    }
</style>
<template>
    <el-main>
        <div class="search-result-wrap">
            <div class="search-result-main">
                <div class="search-result__hd">
                    <div class="result-target-wrap">
                        <div class="result-target-main">
                            <div class="result-target__lt">
                                <div class="music clearfix" v-if="true"> <!--video music 和 video 切换-->
                                    <div class="img-wrap">
                                        <img src="../assets/record.png" alt=""> <!-- music 时图片固定，video 为目标文件封面图 -->
                                    </div>
                                    <div class="name">
                                        {{keyword}}：
                                        <br>
                                        <span class="file-name">其他平台侵权列表</span>
                                    </div>
                                </div>
                                <div class="video clearfix" v-if="false"></div>
                            </div>
                            <div class="result-target__rt">
                                <el-button-group>
                                    <el-button @click="setCurrentCate(value.platform)"
                                               v-for="(value, key, index) in cate"
                                               :type="(currentCate == value.platform) ? 'primary' :'' ">{{value
										.label}}
                                    </el-button>
                                </el-button-group>
                            </div>
                        </div>
                    </div>
                </div>
                <transition name="bounceInUp">
                    <div class="search-result__bd"  v-for="list in dataList" v-if="currentCate === list.platform"  >
                        <el-row class="result-list">
                            <el-col :span="6" class="result-item" v-for="li in list.arr">
                                <div @click="goDetailPage(li.url)" class="item-link">
                                    <div class="item__hd"></div>
                                    <div class="item__bd">
                                        <div class="img-wrap">
                                            <img :src="li.cover" v-if="li.platform != 2" alt="" class="img">
                                            <img src="../assets/weishi_bg.png"  v-if="li.platform == 2"  class="img">
                                        </div>
                                        <!--<img src="../assets/kuaishou.png" alt="" class="img">-->
                                    </div>
                                    <div class="item__ft">
                                        <div class="source">
                                            <div class="img-wrap">
                                                <!--<img src="../assets/vue-logo.png" alt="" class="source-img">-->
                                                <img :src="li.avatar" alt="" class="source-img" v-if="li.platform != 2" >
                                                <img src="../assets/weishi_logo.jpg"   v-if="li.platform == 2"  class="img">
                                            </div>
                                            <span class="icon icon-infringement"></span>
                                        </div>
                                        <span class="name">{{li.author}}</span>
                                    </div>
                                </div>
                            </el-col>
                        </el-row>
                        <el-pagination v-if="list.count !==0"
                                class="mt40" background
                                layout="prev, pager, next"
                                       :page-size="pageNum"
                                       :current-page="list.pageIndex"
                                       @current-change="pageIndexChange"
                                :total="list.count">
                        </el-pagination>
                        <span v-if="list.count ===0"  class="mt40" >
                            暂无数据
                        </span>
                    </div>
                </transition>

                <div class="search-result__ft"></div>
            </div>
        </div>
    </el-main>
</template>

<script>
	export default {
		name: 'List',
		data() {
			return {
				keyword: '你能不能',
				pageNum:12,     // 分页每页多少条
				currentCate: 0,     // 当前平台类型的 platform
                // 所有平台类型
				cate: [
					{label: '全部', platform: 0},
					{label: '快手', platform: 1},
					{label: '微视', platform: 2},
					{label: 'NANI', platform: 3},
					{label: 'BIU', platform: 4},
					{label: '聚美', platform: 5},
				],
                // 所有平台类型的数据集合
				dataList: {
                       data0:{arr:[],count:0,pageIndex:1,platform:0},
                       data1:{arr:[],count:0,pageIndex:1,platform:1},
                       data2:{arr:[],count:0,pageIndex:1,platform:2},
                       data3:{arr:[],count:0,pageIndex:1,platform:3},
                       data4:{arr:[],count:0,pageIndex:1,platform:4},
                       data5:{arr:[],count:0,pageIndex:1,platform:5},
                },
			}
		},
		methods: {
			// 改变当前平台
			setCurrentCate(i) {
				this.currentCate = i;
			},
            // 跳转详情
			goDetailPage(url) {
//				window.location.href = "https://www.linkeddb.com/static/demo/"
				window.open(url)
			},

            // 当前平台的页码发生变化
			pageIndexChange(newPageIndex){
				console.log(':::新页码',newPageIndex);
				console.log(':::当前平台',this.currentCate);
				this.dataList[`data${this.currentCate}`].pageIndex = newPageIndex;
				this.getDataByCate(this.currentCate,newPageIndex)
			},

            // 根据平台获取数据
            getDataByCate(cate,pageIndex){

				// 模拟
//                setTimeout(()=>{
//                	console.log(`初始化${cate},${pageIndex},${this.pageNum}`);
//                    var arr = []
//                    var c = (5-cate)*13;
//	                for (var i = 0; i < (6-cate); i++) {
//		                arr.push(1)
//	                }
//	                this.dataList[`data${cate}`].arr=arr
//	                this.dataList[`data${cate}`].count=c
//	                console.log(this.dataList);
//                },1000);

	            this.$axios.get(`https://copy-api.linkeddb.com/api/search/?keyword=${this.keyword}&page_index=${pageIndex}&page_num=${this.pageNum}&platform=${cate}`).then(data => {
		            console.log(data);
		            if (data.data.code === 200) {
			            this.dataList[`data${cate}`].arr=data.data.list
			            this.dataList[`data${cate}`].count=data.data.count
		            }
	            })

            }
		},
		mounted() {

			// 获取参数 keyword
			if(this.$route.params.keyword){
				this.keyword = this.$route.params.keyword;
            }
			console.log(this.keyword);

            // 初始化依次填充页面数据
			for(var x in this.cate){
				this.getDataByCate(x,1);
            }

		}
	}
</script>




