import Vue from 'vue'
import Router from 'vue-router'
import demo from '@/components/HelloWorld'
import LoginPage from '@/pages/login'
import indexPage from '@/pages/index'
import listPage from '@/pages/list'
import searchPage from '@/pages/search'
import detailPage from '@/pages/detail'
import excelPage from '@/pages/excel'

Vue.use(Router);

export default new Router({

	// mode: 'history',    // 默认 hash 模式 , history 模式可利用 history.pushState API去掉#号
	routes: [
		// 重定向
		{
			path: '/',
			redirect: '/index'
		},
		{
			path: '/login',
			name: 'LoginPage',
			component: LoginPage
		},
		{
			path: '/index',
			name: 'indexPage',
			component: indexPage,
			children:[
				{path:'',name:'searchPage',component:searchPage},
				{path:'list',name:'listPage',component:listPage},
			]
		},
		{
			path: '/detail',
			name: 'detailPage',
			component: detailPage
		},
		{
			path: '/excel',
			name: 'excelPage',
			component: excelPage
		},
		{
			path: '/demo',
			component: demo
		},
        {
            path: '**',   // 错误路由[写在最后一个]
            redirect: '/index'   //重定向
        }
	]
})



